@import url("https://fonts.googleapis.com/css?family=Montserrat:300,400,600|Roboto+Slab");
* {
  box-sizing: border-box; }

html {
  font-size: 16px; }

body {
  font-family: 'Roboto Slab', serif;
  padding: 0;
  margin: 0;
  font-size: 1rem;
  line-height: 1.5rem; }

main {
  width: 90%;
  max-width: 600px;
  margin: 3rem auto; }

h1, h2, h3, h4, h5 {
  color: #30322d;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400; }

h1 {
  margin: 2rem 0 1.5rem;
  font-size: 2.5rem;
  line-height: 3rem;
  font-weight: 300; }

h2 {
  margin: 2rem 0 1rem;
  font-size: 2rem;
  line-height: 2.5rem; }

h3 {
  margin: 1rem 0 0.5rem;
  font-size: 1.5rem;
  line-height: 2rem;
  font-weight: 600; }

h4 {
  margin: 0.5rem 0;
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 600; }

h5 {
  margin: 0.5rem 0;
  font-size: 1rem;
  line-height: 1.5rem;
  font-weight: 400; }

hgroup {
  margin: 1rem 0; }
  hgroup h1, hgroup h2, hgroup h3, hgroup h4, hgroup h5 {
    margin: 0; }

p, ul, ol, iframe, blockquote {
  margin: 1rem 0;
  font-size: 1rem;
  line-height: 1.5rem; }
  p code, ul code, ol code, iframe code, blockquote code {
    line-height: 0.5rem; }

ul, ol {
  padding: 0 0 0 1rem; }

footer img {
  height: 24px; }

section {
  padding: 1rem 0; }

hr {
  margin: 2rem 0; }

sup.footnote-ref {
  font-size: 1rem;
  line-height: 0.5rem; }

blockquote {
  border-left: 6px rgba(1, 1, 1, 0.1) solid;
  background-color: rgba(1, 1, 1, 0.1);
  padding-left: 1rem;
  padding-top: 0.5rem;
  padding-bottom: 0.5rem; }
